.sectionn-registration-block {
    @include section-padding;
}

.registration-block {
    max-width: 760px;
    margin: 0 auto;

    &__title {
        @include section-title;
        padding-bottom: 30px;
    }

    &__photo {
        padding-bottom: 30px;
    }

    &__form {

        & .btn {
            width: 100%;

            @media screen and (min-width: $w576) {
                width: auto;
                margin-left: auto;
            }
        }

    }
}

.box-photo {


    &__image {

        max-width: 115px;
        margin: 0 auto;
        width: 100%;
        background-color: #EBEBEB;
        border-radius: 50%;
        background-size: 30% auto;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url(data:image/svg+xml;base64,);


        & span {
            padding-top: 100%;
            @include bgimage_cover;
            border-radius: 50%;
        }
    }

    &__add {
        text-align: center;
        padding-top: 10px;
    }
}

.attach-text {
    & input {
        display: none;
    }

    & span {
        display: inline-block;
        text-decoration: underline;
        color: $main;
        padding: 0.5em 1em;
        cursor: pointer;

        &:hover {
            background-color: #EBEBEB;
            text-decoration: none;
        }
    }
}